.form {
  padding: 0 var(--side-offset, 20px);
  --grad-stop: 33.5%;
  display: grid;
  justify-content: end;
  padding-top: 157px;
  padding-bottom: 250px;

  background: {
    color: #00b3ff;
    image: url("../img/paperairplane.png"), linear-gradient(139deg, rgba(4, 24, 39, 0.5) var(--grad-stop), transparent var(--grad-stop));

    size: auto, 100%;
    repeat: no-repeat;

    position: 25% 241px, 0 0;
  }

  @media (max-width: 1440px) {
    background: #00b3ff;
  }

  @media (max-width: 1024px) {
    background: #00b3ff;
    padding-top: 46px;
    padding-bottom: 122px;
    justify-content: start;
  }

  @media (max-width: 768px) {
    justify-content: stretch;
    padding-bottom: 80px;
  }
}

.form h3 {
  font-family: $ff-accent;
  font-weight: 400;
  font-size: 36px;
  line-height: 51px;
  margin: 0 51px 0 0;
  color: #ffffff;

  @media (max-width: 1024px) {
    margin: 0;
    font-size: 36px;
    line-height: 51px;
  }

  @media (max-width: 768px) {
    font-size: 30px;
    line-height: 140.62%;
  }
}

.form p {
  padding: 0;
  margin: 0;
  margin-top: 11px;
  margin-left: 5px;
  font-weight: 400;
  line-height: 22px;
  color: #ffffff;

  @media (max-width: 1024px) {
    margin-top: 7px;
    margin-left: 0;
  }

  @media (max-width: 768px) {
    font-weight: 400;
    font-size: 14px;
    line-height: 134.4%;
    margin-left: 0;
  }
}

.form__items {
  display: grid;
  margin-left: auto;
  margin-right: 10px;
  margin-top: 38px;
  width: 100%;
  max-width: 580px;
  grid-template-columns: 1fr 1fr;
  column-gap: 30px;
  row-gap: 20px;

  @media (max-width: 1024px) {
    margin-left: 0;
    margin-bottom: 0;
    max-width: 670px;
    margin-top: 27px;
  }

  @media (max-width: 768px) {
    max-width: none;
    display: flex;
    flex-direction: column;
    margin-top: 36px;
  }
}

.form__question {
  grid-column-start: 1;
  grid-column-end: 3;
}

.form__button {
  width: 100%;
  max-width: 263px;
  margin-top: 8px;
  border: none;
  padding: 14px 25px;
  font-family: $ff-accent;
  font-weight: 700;
  line-height: 19px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #ffffff;
  background: #041427;
  border-radius: 3px;

  @media (max-width: 1024px) {
    margin-top: 6px;
    max-width: 265px;
  }

  @media (max-width: 768px) {
    max-width: none;
    width: 100%;
    margin-top: 4px;
  }
}

.form__evidence {
  display: flex;
  margin: 0;
  font-weight: 400;
  font-size: 14px;
  color: #ffffff;
  opacity: 0.8;
  align-items: center;

  @media (max-width: 768px) {
    margin-top: 0;
  }
}
